import React from 'react';
import { Document, Page } from 'react-pdf';


const PdfViewer = ({ pdfUrl, numPages, currentPage, onPageChange }) => {
    const handlePageChange = (newPage) => {
        onPageChange(newPage);
    };

    return (
        <div className="pdf-viewer">
            <div className="pdf-viewer-pagination">
                <button
                    className="pdf-viewer-pagination-btn"
                    disabled={currentPage === 1}
                    onClick={() => handlePageChange(currentPage - 1)}
                >
                    &lt; Prev
                </button>
                <p>
                    Page {currentPage} of {numPages}
                </p>
                <button
                    className="pdf-viewer-pagination-btn"
                    disabled={currentPage === numPages}
                    onClick={() => handlePageChange(currentPage + 1)}
                >
                    Next &gt;
                </button>
            </div>
            <div className="pdf-viewer-document">
                <Document file={pdfUrl} onLoadSuccess={numPages}>
                    {Array.from(new Array(numPages), (el, index) => (
                        <Page key={index + 1} pageNumber={index + 1} />
                    ))}
                </Document>
            </div>
        </div>
    );
};

export default PdfViewer;

